export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		}
	},
	onShow () {
		var _this = this
		var globalUser = _this.globalUser("globalUser")
		if (globalUser != null ) {
			_this.userIsLogin = true
			_this.userInfo = globalUser
		} else {
			_this.userIsLogin = false
			_this.userInfo = {}
		}
	},
	methods: {
		
	}
}
